export default {
	methods: {
		createTour(){
			this.tour = this.$shepherd({
				useModalOverlay: true
			});
		},
		addReportTour() {
			this.tour.addSteps([
				{
					id: 'report_step_1',
					text: 'Так выглядит Ваш отчет',
					attachTo: { 
						element: '#home-page > div > section > div.animate__animated.animate__fadeIn.velmld-parent',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteReportTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'report_step_2',
					text: 'Это столбец с встроенной сортировкой, при нажатии на него столбец будет отфильтрован по дате (по убыванию/по возрастанию/выключить сортировку)',
					attachTo: { 
						element: '#vgt-table > thead > tr > th.vgt-right-align.text-center.sortable > button',
						on: 'top'
					},
					canClickTarget: true,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteReportTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'report_step_3',
					text: 'Таблица поддерживает экспорт в Excell файл',
					attachTo: { 
						element: '#table_export_excel',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteReportTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'report_step_4',
					text: 'Также вы можете отфильтровать данные по периоду',
					attachTo: { 
						element: '#table_datepicker',
						on: 'top'
					},
					advanceOn: { 
						selector: '#table_datepicker', 
						event: 'click'
					},
					canClickTarget: true,
				},
				{
					id: 'report_step_5',
					text: 'Выберите начало периода',
					attachTo: { 
						element: '#app > div.v-dialog__content.v-dialog__content--active > div > div > div > div.v-picker__body.theme--light > div > div.v-date-picker-table.v-date-picker-table--date.theme--light > table > tbody > tr:nth-child(3) > td:nth-child(4) > button',
						on: 'top'
					},
					advanceOn: { 
						selector: '#app > div.v-dialog__content.v-dialog__content--active > div > div > div > div.v-picker__body.theme--light > div > div.v-date-picker-table.v-date-picker-table--date.theme--light > table > tbody > tr:nth-child(3) > td:nth-child(4) > button', 
						event: 'click'
					},
					canClickTarget: true,
				},
				{
					id: 'report_step_6',
					text: 'Выберите конец периода',
					attachTo: { 
						element: '#app > div.v-dialog__content.v-dialog__content--active > div > div > div > div.v-picker__body.theme--light > div > div.v-date-picker-table.v-date-picker-table--date.theme--light > table > tbody > tr:nth-child(5) > td:nth-child(4) > button',
						on: 'top'
					},
					advanceOn: { 
						selector: '#app > div.v-dialog__content.v-dialog__content--active > div > div > div > div.v-picker__body.theme--light > div > div.v-date-picker-table.v-date-picker-table--date.theme--light > table > tbody > tr:nth-child(5) > td:nth-child(4) > button', 
						event: 'click'
					},
					canClickTarget: true,
				},
				{
					id: 'report_step_7',
					text: 'Фильтруем',
					attachTo: { 
						element: '#date_picker__btn-filter',
						on: 'top'
					},
					advanceOn: { 
						selector: '#date_picker__btn-filter', 
						event: 'click'
					},
					canClickTarget: true,
				},
				{
					id: 'report_step_7',
					text: 'Это все, удачного пользования!',
					canClickTarget: true,
					buttons: [
						{
							text: 'Закончить',
							action() {
								localStorage.setItem('CompleteReportTour', 'true');
								this.complete();
							}
						},
					]
				},
			]);
		},
		addAdminTour() {
			this.tour.addSteps([
				{
					id: 'admin_step_1',
					text: 'Это ваш список клиентов',
					attachTo: { 
						element: '#home-page > div > section > div.animate__animated.animate__fadeIn.velmld-parent',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_2',
					text: 'Вы можете просматривать шаблоны клиента',
					attachTo: { 
						element: '#home-page > div > section > div.wrapper.animate__animated.animate__fadeIn.velmld-parent > div.v-virtual-scroll.virual-scroll > div > div:nth-child(1) > div > div.list_item__block_buttons > button:nth-child(1)',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_2',
					text: 'также просматривать отчеты',
					attachTo: { 
						element: '#home-page > div > section > div.wrapper.animate__animated.animate__fadeIn.velmld-parent > div.v-virtual-scroll.virual-scroll > div > div:nth-child(1) > div > div.list_item__block_buttons > button:nth-child(2)',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_3',
					text: 'и забанить клиента. У забаненого клиента не будет доступа к его учетной записи. (ее можно вернуть повторным кликом на кнопку)',
					attachTo: { 
						element: '#home-page > div > section > div.wrapper.animate__animated.animate__fadeIn.velmld-parent > div.v-virtual-scroll.virual-scroll > div > div:nth-child(1) > div > div.list_item__block_buttons > button:nth-child(3)',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_4',
					text: 'Давайте посмотрим как создать клиента',
					attachTo: { 
						element: '#home-page > div > div > div.dashboard__control > div > button:nth-child(1)',
						on: 'top'
					},
					advanceOn: { 
						selector: '#home-page > div > div > div.dashboard__control > div > button:nth-child(1)', 
						event: 'click'
					},
					canClickTarget: true,
				},
				{
					id: 'admin_step_5',
					text: 'При помощи данной формы можно создать клиента',
					attachTo: { 
						element: '#home-page > div > section',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_6',
					text: 'Вводим номер телефона через 8 который играет роль логина (пример: 87471231212)',
					attachTo: { 
						element: '#home-page > div > section > div.wrapper.animate__animated.animate__fadeIn.velmld-parent > form > div > div:nth-child(1) > div > div:nth-child(1) > div > div.v-input__slot > div',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_7',
					text: 'заполняем остальные поля',
					attachTo: { 
						element: '#general-fields',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_8',
					text: 'Не забываем отправить',
					attachTo: { 
						element: '#home-page > div > section > div.wrapper.animate__animated.animate__fadeIn.velmld-parent > form > div > div:nth-child(2) > div > button',
						on: 'top'
					},
					canClickTarget: false,
					buttons: [
						{
							text: 'Выйти',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.cancel();
							}
						},
						{
							text: 'Далее',
							action: this.tour.next
						},
					]
				},
				{
					id: 'admin_step_9',
					text: 'Обучение окончено, приятного пользования!',
					canClickTarget: false,
					buttons: [
						{
							text: 'Закончить',
							action() {
								localStorage.setItem('CompleteAdminTour', 'true');
								this.complete();
							}
						},
					]
				},
			]);
		}
	}
};